class GUI {
	constructor() {
		this.initialize();
		this.calculate();

		this.cyclicMode = false;
	}

	initialize(vals = [1, 2, 3, 4]) {
		this.init = {};
		this.init['quickFrog'] = {'draw': false, 'obj': {}};

		const guiRef = this;

		for (const [j, letter] of Object.entries(['Q', 'W', 'E', 'R'])) {
			const i = vals[Number(j)];

			this.init['quickFrog']['obj'][letter + 'Button'] = {
				frogBound: i,

				x: (Number(j)) / 8 + Number(j) / 128,
				y: 1 / 32,

				width: 1 / 8,
				aspect: 'square',
				cornerRadius: 25,

				image: gfx['button'],
				fitImage: true,
				imageScale: .95,
				stroke: color(0, 0),
				color: color(0, 0),

				textColor: '#000',
				textFont: pixelFont,
				textSize: 32,
				textScaled: true,
				text: '\n\n' + i.toString(),

				onOutside: function() {
					// (TEMP?) BUG FIX
					// verify mouse button isn't in invalid range
					// this prevents shaking
					let mouseValidY = (mouseY < this.y) /* top bound */ || (mouseY > 2 * this.y + this.height) /* lower bound */;
					let mouseValidX = (mouseX < this.x) /* left bound*/ || (mouseX > this.x + this.width)      /* right bound */; // standard stuff
					let mouseValid = mouseValidX || mouseValidY;

					if (mouseValid && !buttonPressed && puzzleMode) {
						cursor('default');
						this.image = gfx.button;
						this.stroke = color(0, 0);
						this.color = color(0, 0);
						this.imageScale = .95;
						this.y = screenSizeY / 32;
						frog.type = 0;
					}
				},

				onHover: function() {
					if (!buttonPressed && puzzleMode) {
						cursor('grab');
						this.image = gfx.buttonToggled;
						this.imageScale = .97;
						this.y = screenSizeY / 64;
						frog.type = this.frogBound;
					}
				},

				onPress: function() {
					if (!buttonPressed && puzzleMode) {
						buttonPressed = true;
						frog.type = this.frogBound;
						this.stroke = 'rgba(100, 255, 100, 1)';
						this.color = 'rgba(100, 255, 100, 1)';
						this.imageScale = .98;

						//if (!guiRef.cyclicMode)
						controlsEnabled = true;
					}
				},

				extraDraw: function() {
					// draw froggy grid
					const angle = 2 * PI / this.frogBound;

					for (let i = 0; i < this.frogBound; i++) {
						let x = this.x + this.width / 5 * (2.1 + Math.cos(angle * i));
						let y = this.y + this.width / 5 * (1.5 + Math.sin(angle * i));

						image(gfx['bby'], x, y);
					}
				}
			};
		}

		this.init['buttons'] = {
			draw: false,

			obj: {
				'inventoryButton': {
					x: 1 / 64,
					y: 1 / 32,

					width: 1 / 24,
					aspect: 'square',

					image: gfx['inventoryButton'],
					fitImage: true,
					text: '',

					onOutside: function() {
						this.color = '#000';
					},

					onHover: function() {
						this.color = '#222';
					},

					onRelease: function() {
						player.gui.obj.quickFrog.draw = !player.gui.obj.quickFrog.draw;
					},

					extraDraw: function() {}
				}
			}
		};
	}

	calculate() {
		this.obj = {};

		for (const [typeName, objType] of Object.entries(this.init)) {
			this.obj[typeName] = {draw: objType.draw, obj: {}};

			for (const [objName, obj] of Object.entries(objType.obj)) {
				// scale to canvas size
				obj.x *= screenSizeX;
				obj.y *= screenSizeY;

				obj.width *= screenSizeX;

				if (obj.aspect == 'square') obj.height = obj.width;
				else obj.height *= screenSizeY;

				// copy properties to Clickable object
				this.obj[typeName]['obj'][objName] = new Clickable();
				Object.assign(this.obj[typeName]['obj'][objName], obj);
			}
		}
	}

	// frame() 

	draw() {
		for (const [typeName, objType] of Object.entries(this.obj)) {
			if (objType.draw) {
				for (const [objName, obj] of Object.entries(objType['obj'])) {
					obj.draw();
					obj.extraDraw();
				}
			}
		}
	}
}